import React from 'react';
import '../stylesheets/charts.css';
import Header from "../components/header";
import Footer from "../components/footer";
import {Link} from "react-router-dom";
import ChartListTemp from "../components/chartListTemp";
import PlayURL from "../components/getPlaylist";


class ChartsHour extends React.Component {
    render() {
        return (
            <div className="container">
                <Header/>
                <div className="fade-in-page">
                    <div className="news-header">
                        <h2>BILLBOARDOO CHART</h2>
                        <h1>MONTHLY 100</h1>
                    </div>
                    <hr className="chart-menu-line"/>
                    <div className="chart-menu">
                        <Link to="/charts" className="chart-menu-content">누적</Link>
                        <Link to="/charts/hourly" className="chart-menu-content">시간</Link>
                        <Link to="/charts/daily" className="chart-menu-content">일간</Link>
                        <Link to="/charts/weekly" className="chart-menu-content">주간</Link>
                        <Link to="/charts/monthly" className="chart-menu-content">월간</Link>
                    </div>
                    <hr className="chart-menu-line"/>
                    <div className="chart-info">
                        <Link to="/graphs/monthly" className="chart-menu-content">그래프로 보기</Link>
                        {<PlayURL type="monthly"/>}
                    </div>
                    <div className="chart-wrap">
                        <hr className="chart-separate-line"/>
                        <div className="chart-header">
                            <span id="header-pos">순위</span>
                            <span id="header-title">제목</span>
                            <span id="header-artist">가수</span>
                            <div className="header-right-align">
                                <span id="header-peak">지난주</span>
                                <span id="header-date">날짜</span>
                                <span id="header-views">{this.props.type === "total" ? "조회수": "증가량"}</span>
                            </div>
                        </div>
                        <hr className="chart-separate-line"/>
                        <ChartListTemp type="monthly"/>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

export default ChartsHour;
