import React, {useState, useEffect} from "react";
import axios from "axios";
import {Link} from "react-router-dom";


function PlayURL(props) {
    const [data, setData] = useState();
    useEffect(() => {
        async function get() {
            const result = await axios.get(
                `/api/charts/${props.type}/100`
            );
            setData(result.data);
        }
        get().then();
    }, [props.type]);

    function getURL() {
        let url = `/player/${data[0].id}?prev=&next=`;
        for (let i = 1; i < data.length; i++) {
            url += `${data[i].id}${i === data.length - 1 ? "" : ","}`;
        }
        return url;
    }

    if (!data || data[0].increase === 0) return null;
    return (
        <Link to={getURL()} className="go-playlist fade-in-page">1~100위까지 듣기</Link>
    )
}

export default PlayURL;