import React, {useState, useEffect} from "react";
import axios from "axios";
import {Link} from "react-router-dom";


function ArtistAlbums(props) {
    const [data, setData] = useState();
    useEffect(() => {
        async function get() {
            const result = await axios.get(
                `/api/albums/${props.artist}`
            );
            setData(result.data);
        }

        get().then();
    }, [props.artist]);

    function convertDate(date) {
        date = String(date);
        let year = date.slice(0, 2);
        let month = date.slice(2, 4);
        let day = date.slice(4, 6);
        return `20${year}.${month}.${day}`;
    }

    if (!data) return null;
    return (
        <div className="artist-albums fade-in-page">
            <div className="artist-album-count">총 {data.length}곡</div>
            {data.map(d => (
                <Link to={`/player/${d.id}`}>
                    <div className="artist-albums-body">
                        <img src={d.image} alt="cimg" className="chart-image artist-albums-image"/>
                        <span className="chart-title artist-albums-name">{d.title}</span>
                        <span className="chart-artist artist-albums-artist">{d.artist}</span>
                        <div className="right-align">
                            <div className="chart-date artist-albums-date">{convertDate(d.date)}</div>
                        </div>
                    </div>
                </Link>
            ))}
        </div>
    )
}

export default ArtistAlbums;